@use "global" as *;

.c-scrollbtn {
  position: relative;
  display: inline-block;
  // padding-left: 20px;
  color: #333;
  text-decoration: none;
}
.c-scrollbtn:before {
  content: '';
  width: 40px;
  height: 40px;
  // background: #333;
  border: 1px solid $blue;
  
  border-radius: 50%;
  position: absolute;
  top: 50%;
  right: 5px;
  margin-top: -9px;
}
.c-scrollbtn:after {
  content: '';
  width: 10px;
  height: 10px;
  border: 0;
  border-top: solid 1px $blue;
  border-right: solid 1px $blue;
  position: absolute;
  top: 8px;
  right: 19px;
  // margin-top: -4px;
  transform: rotate(-45deg);
}